.header {
  display: flex;
  background-color: #3f0f40;
  color: white;
  align-items: center;
  text-align: center;
  height: 30px;
  justify-content: center;
}

.app-body {
  display: flex;
  height: 96.2vh
}

.sidebar {
  background-color: #3f0f40;
  color: white;
  border-top: 1px solid#49274b;
  flex: 0.3;
  max-width: 260px;
  padding: 12px;
}

.border-bottom {
  border-bottom: 1px solid#49274b;
}

.sidebar-header > h3 {
  display: flex;
  align-items: center;
  font-size: 14px;
}

.sidebar-header > h3 > .MuiSvgIcon-root {
  color: green;
}

.chat {
  flex: 1;
  height: 90%;
  overflow: scroll;
  overflow-x: hidden;
}

.chat-header {
  padding: 20px;
    border-bottom: 1px solid gray;
}

.channel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.channel > .MuiSvgIcon-root {
  cursor: pointer;
}

.message {
  display: flex;
  align-items: center;
  padding: 8px;
}

.message > img {
height: 50px;
width: 50px;
object-fit: contain;
}

.message-time {
  color: gray;
  font-weight: 300;
  font-size: 13px;
}

.chat-input {
  border-radius: 20px;
}

.chat-input > form {
  display: flex;
  position: relative;
  justify-content: center;
}

.chat-input > form > input {
  position: fixed;
  bottom: 30px;
  width: 70%;
  border: 1px solid gray;
  border-radius: 3px;
  padding: 10px;
}

.chat-input > button {
  position: fixed;
  bottom: 32px;
  left: 91.5%;
}

.channel-name {
  padding: 5px;
  cursor: pointer;
}

.welcome {
  text-align:center;
  padding-top: 100px;
}